<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>全部图文</span>
      </div>
      <div class="text item">
        <!--el-form搜索表单区域-->
        <el-form ref="searchFormRef" :model="searchForm" label-width="100px">
          <el-form-item label="文章状态：">
            <el-radio-group v-model="searchForm.status">
              <el-radio label>全部</el-radio>
              <el-radio label="0">草稿</el-radio>
              <el-radio label="1">待审核</el-radio>
              <el-radio label="2">审核通过</el-radio>
              <el-radio label="3">审核失败</el-radio>
              <!--0-草稿，1-待审核，2-审核通过，3-审核失败，4-已删除，不传为全部-->
              <el-radio label="4" disabled>已删除</el-radio>
            </el-radio-group>
          </el-form-item>
          <!-- <el-form-item label="频道列表：">
            <el-select v-model="searchForm.channel_id" placeholder="请选择">
              <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item> -->
          <pinDao :son="searchForm.channel_id" @slt="getSonId"></pinDao>
          <el-form-item label="时间选择：">
            <div class="block">
              <el-date-picker
                v-model="totime"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                value-format="yyyy-MM-dd"
              ></el-date-picker>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="box-card" style="margin-top:20px">
      <div class="text item">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column label="头像" width="180">
            <img
              :src="stData.row.cover.images[0] || 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1573624120194&di=8c90a369173bef77f96523d3ee74e2bd&imgtype=0&src=http%3A%2F%2Fn8.cmsfile.pg0.cn%2Fgroup4%2FM00%2F03%2F17%2FCgoOFlnbBveAYMDQAACYNd32mDA603.jpg'"
              slot-scope="stData"
              alt="没有头像"
              width="80"
              height="80"
              style="  border-radius: 50%"
            />
          </el-table-column>
          <el-table-column prop="title" label="姓名" width="180"></el-table-column>
          <el-table-column label="状态" prop="status">
            <template slot-scope="stData">
              <!--0-草稿，1-待审核，2-审核通过，3-审核失败，4-已删除，不传为全部-->
              <el-button type="primary" v-show="stData.row.status===0" size="mini">草稿</el-button>
              <el-button type="info" v-show="stData.row.status===1" size="mini">待审核</el-button>
              <el-button type="success" v-show="stData.row.status===2" size="mini">审核通过</el-button>
              <el-button type="warning" v-show="stData.row.status===3" size="mini">审核失败</el-button>
              <el-button type="danger" v-show="stData.row.status===4" size="mini">已删除</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="pubdate" label="时间"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="setData">
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                size="mini"
                @click.native="eild(setData.row.id)"
              ></el-button>
              <el-button
                type="danger"
                icon="el-icon-delete"
                circle
                size="mini"
                @click.native="del(setData.row.id)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <div class="item text clearfix bottom">
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="searchForm.page"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="searchForm.per_page"
            layout="total, sizes, prev, pager, next, jumper"
            :total="tot"
          ></el-pagination>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import pinDao from '@/components/pindao.vue'
export default {
  name: 'Article',
  created () {
    this.getArticle()
  },
  components: {
    pinDao
  },
  data () {
    return {
      tot: 0, // 记录总条数
      options: null,
      searchForm: {
        channel_id: '',
        status: '',
        begin_pubdate: '',
        end_pubdate: '',
        page: 1,
        per_page: null
      },
      totime: '',
      tableData: null
    }
  },

  watch: {
    totime (newval) {
      if (newval) {
        this.searchForm.begin_pubdate = newval[0]
        this.searchForm.end_pubdate = newval[1]
      } else {
        this.searchForm.begin_pubdate = ''
        this.searchForm.end_pubdate = ''
      }
    },
    searchForm: {
      handler (newv, oldv) {
        if (newv) {
          // 根据用户条件选择  文档信息
          this.$http
            .get('/articles', {
              params: this.FormData()
            })
            .then(result => {
              this.tableData = result.data.data.results
              this.tot = result.data.data.total_count
            })
        }
      },
      deep: true,
      immediate: true
    }
  },

  methods: {
    // 这个方法明明没有调用，为啥就执行了
    getSonId (val) {
      this.searchForm.channel_id = val
    },
    FormData () {
      let data = this.searchForm
      let FormData = {}
      for (let i in data) {
        if (data[i]) {
          FormData[i] = data[i]
        }
      }
      return FormData
    },

    getArticle () {
      this.$http
        .get('/articles')
        .then(result => {
          this.tableData = result.data.data.results
          this.tot = result.data.data.total_count
        })
        .catch(error => {
          console.log(error)
        })

      this.$http
        .get('/channels')
        .then(result => {
          this.options = result.data.data.channels
        })
        .catch(error => {
          console.log(error)
        })
    },

    // 分页器------------------------------------
    handleSizeChange (val) {
      this.searchForm.per_page = val
    },

    handleCurrentChange (val) {
      this.searchForm.page = val
    },
    // 分页---------------------------------
    // 删除按钮
    del (id) {
      this.$confirm('确认删除吗', '确认信息', {
        distinguishCancelAndClose: true,
        cancelButtonText: '取消',
        confirmButtonText: '确定'
      })
        .then(() => {
          this.$http
            .delete(`/articles/${id}`)
            .then(result => {
              console.log(result)
              this.getArticle()
            })
            .catch(err => {
              return console.log(err)
            })
        })
        .catch(action => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },
    eild (id) {
      this.$router.push(`./amend${id}`)
    }
  }
}
</script>

<style lang="less" scoped>
.el-pagination {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
