<template>
    <el-form-item label="频道列表：">
      <el-select  v-model="id" placeholder="请选择">
        <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
      </el-select>
    </el-form-item>
</template>

<script>
export default {
  name: 'pindao',
  data () {
    return {
      id: '',
      options: ''
    }
  },
  props: {
    son: {
      default: 0
    }
  },
  watch: {
    son (newv, oldv) {
      this.id = newv
    },
    id (newv, oldv) {
      this.$emit('slt', newv)
    }
  },
  created () {
    this.$http
      .get('/channels')
      .then(result => {
        this.options = result.data.data.channels
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>

<style lang="less" scoped>
</style>
